import React, { useEffect, useRef } from 'react';
import './InvitationText.css';  // CSS 파일 임포트

const InvitationText = () => {
  const textRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          textRef.current.classList.add('show');  // 요소가 화면에 보일 때 'show' 클래스 추가
        } else {
          textRef.current.classList.remove('show');  // 요소가 화면에서 사라질 때 'show' 클래스 제거
        }
      },
      { threshold: 0.2 }  // 20%가 화면에 보일 때 트리거
    );

    if (textRef.current) {
      observer.observe(textRef.current);  // ref가 연결된 요소 관찰 시작
    }

    return () => {
      if (textRef.current) {
        observer.unobserve(textRef.current);  // 컴포넌트 언마운트 시 관찰 해제
      }
    };
  }, []);

  return (
    <div
      ref={textRef}  // ref로 연결
      className="invitation-text"
    >
      <h2>INVITATION</h2>
      <p>서로 다른 길을 걸어온 저희가 함께하는</p>
      <p>다섯 번째 겨울을 끝으로</p>
      <p>부부의 연을 맺어 항상 함께하기로 했습니다</p>
      <p className="extra-space">인생의 또 다른 시작을 하기로 한 날</p>
      <p>귀한 걸음 하시어 축복해 주시면 감사하겠습니다</p>
      <div className="names">
        <p>성천모 . 이미자의 아들 성기덕</p>
        <p>노장열 . 주정하의 딸 노승혜</p>
      </div>
    </div>
  );
};

export default InvitationText;
